import { Directive, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output } from '@angular/core';

import { Gate } from '../../../../common/cdk/component/gate';
import { StructureId } from '../../../core/api/structure.id';
import { EditemItemValues } from '../../core/api/event/editem-item.values';
import { SourceCommandInvoker } from '../../core/api/source.command-invoker';
import { SourceEventService } from '../../core/api/event/source-event.service';
import { NgChanges } from '../../../../common/cdk/component/ng-changes';

@Directive()
export abstract class SourceGate extends Gate implements OnChanges, OnInit, OnDestroy {

	@Input()
	source: Array<any> = [];

	@Input()
	items: Array<any> = [];

	@Output()
	sourceEdited: EventEmitter<EditemItemValues> = new EventEmitter();

	protected constructor(protected readonly structureId: StructureId,
						  protected readonly sourceCommandInvoker: SourceCommandInvoker,
						  protected readonly sourceEventService: SourceEventService) {
		super();
	}

	ngOnChanges(changes: NgChanges<SourceGate>) {

		/**
		 * Setting source should be last step
		 */
		if (this.isDefined('source', changes)) {
			this.sourceCommandInvoker.setOrigin(this.source, this.structureId);
		}

		if (this.isDefined('items', changes)) {
			this.sourceCommandInvoker.setOrigin(this.items, this.structureId);
		}
	}

	ngOnInit() {

		this.subscribeAndEmit(
			this.sourceEventService.onSourceEdited(this.structureId),
			this.sourceEdited
		);
	}

	ngOnDestroy() {
		super.ngOnDestroy();
	}

}
