import { Directive, Input, OnChanges } from '@angular/core';
import { StructureId } from '../../../../core/api/structure.id';
import { Gate } from '../../../../../common/cdk/component/gate';
import { FieldConfig } from '../../../../field/core/api/field.config';
import { FieldCommandInvoker } from '../../../../field/core/api/field.command-invoker';
import { NgChanges } from '../../../../../common/cdk/component/ng-changes';


@Directive({
	selector: 'gui-list-view[fields]'
})
export class ListViewFieldGate extends Gate implements OnChanges {

	@Input()
	fields: Array<FieldConfig>;

	constructor(private readonly structureId: StructureId,
				private readonly fieldCommandDispatcher: FieldCommandInvoker) {
		super();
	}

	ngOnChanges(changes: NgChanges<ListViewFieldGate>) {

		if (this.isDefined('fields', changes)) {

			this.fieldCommandDispatcher.initFields(this.fields, this.structureId);
		}
	}

}
