import { AggregateId, ReadModelRootId } from '@generic-ui/hermes';
import { ListViewReadModelRootId } from '../api/list-view.read-model-root-id';

export class ListViewId extends AggregateId {

	constructor(id: string) {
		super(id);
	}

	toReadModelRootId(): ReadModelRootId {
		return new ListViewReadModelRootId(this.getId());
	}
}
