import { CellEditConfig } from '../../api/edit/cell-edit.config';

export class CellEditorManager {

	private readonly enabled: boolean = false;

	private readonly rowEdit: (value: any, item: any, index: number) => boolean = () => true;

	private readonly cellEdit: (value: any, item: any, index: number) => boolean = () => true;

	constructor(config: CellEditConfig) {

		if (config.enabled !== undefined) {
			this.enabled = config.enabled;
		}

		if (config.rowEdit !== undefined) {
			this.rowEdit = config.rowEdit;
		}

		if (config.cellEdit !== undefined) {
			this.cellEdit = config.cellEdit;
		}
	}

	isEnabled(value: any, item: any, index: number): boolean {
		return this.enabled && this.rowEdit(value, item, index) && this.cellEdit(value, item, index);
	}

}
