import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { GuiSelectOption } from '@generic-ui/fabric';

import { SmartComponent } from '../../../../../common/cdk/component/smart-component';
import { ListViewMode } from '../../../core/domain/mode/list-view-mode';
import { ListViewModeArchive } from '../../../core/api/mode/list-view.mode.archive';
import { ListViewReadModelRootId } from '../../../core/api/list-view.read-model-root-id';
import { ListViewWarehouse } from '../../../core/api/list-view.warehouse';


@Component({
	selector: 'div[gui-list-mode-select]',
	templateUrl: './list-view.container-mode-select.component.html',
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ListViewContainerModeSelectComponent extends SmartComponent implements OnInit {

	options: Array<GuiSelectOption> = Object.keys(ListViewMode)
											.map((key: string) => ListViewMode[key])
											.filter((val) => !Number.isInteger(val))
											.map((val) => {
												return this.toGuiSelectOption(val);
											});

	listContainerMode: GuiSelectOption;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly listViewReadModelRootId: ListViewReadModelRootId,
				private readonly listViewWarehouse: ListViewWarehouse,
				private readonly listViewModeRepository: ListViewModeArchive) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.hermesSubscribe(
			this.listViewWarehouse.onMode(this.listViewReadModelRootId.toAggregateId()),
			(mode: ListViewMode) => {
				this.listContainerMode = this.toGuiSelectOption(mode);
			}
		);
	}

	changeContainerMode(mode: GuiSelectOption): void {
		const selectedMode = this.toListViewMode(mode.value);
		this.listViewModeRepository.next(this.listViewReadModelRootId.toAggregateId(), selectedMode);
	}

	private toGuiSelectOption(value: string | ListViewMode): GuiSelectOption {
		return { value, name: value };
	}

	private toListViewMode(mode: string): ListViewMode {

		switch (mode) {
			case 'List':
				return ListViewMode.LIST;

			case 'Card':
				return ListViewMode.CARD;

			default:
				return ListViewMode.LIST;
		}
	}

	protected getSelectorName(): string {
		return 'gui-list-mode-select';
	}
}
