import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { ListViewMode } from '../../core/domain/mode/list-view-mode';
import { ListViewReadModelRootId } from '../../core/api/list-view.read-model-root-id';
import { ListViewWarehouse } from '../../core/api/list-view.warehouse';


@Component({
	selector: 'div[gui-list-view-layout]',
	templateUrl: './list-view-layout.component.html',
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ListViewLayoutComponent extends SmartComponent implements OnInit {

	listModeEnabled: boolean = false;

	cardModeEnabled: boolean = false;

	selectorEnabled: boolean = false;

	searchBarEnabled: boolean = true;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly listViewReadModelRootId: ListViewReadModelRootId,
				private readonly listViewWarehouse: ListViewWarehouse
	) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.hermesSubscribe(
			this.listViewWarehouse.onMode(this.listViewReadModelRootId.toAggregateId()),
			(mode: ListViewMode) => {
				this.listModeEnabled = mode === ListViewMode.LIST;
				this.cardModeEnabled = mode === ListViewMode.CARD;
			}
		);

		this.hermesSubscribe(
			this.listViewWarehouse.onSelector(this.listViewReadModelRootId.toAggregateId()),
			(enabled: boolean) => {
				this.selectorEnabled = enabled;
			}
		);
	}

	protected getSelectorName(): string {
		return 'gui-list-view-layout';
	}
}
