import { TranslationChange } from '../../../../l10n/core/api/translation-change';
import { defaultTranslation } from '../../../../l10n/core/api/default-translation';

export interface GuiListPaging {

	enabled?: boolean;

	page?: number;

	pageSize?: number;

	pageSizes?: Array<number>;

	pagerTop?: boolean;

	pagerBottom?: boolean;

}

export enum GuiListMode {
	LIST,
	CARD
}

export interface GuiListView {

	active?: GuiListMode;

	selector?: boolean;

}

export interface GuiListField {

	field?: string;

	type?: GuiListFieldType;

}

export enum GuiListFieldType {
	UNKNOWN,
	NUMBER,
	STRING,
	BOOLEAN,
	DATE,
	CUSTOM
}

export interface GuiListSearching {

	enabled?: boolean;

	placeholder?: string;

	phrase?: string;

}

export interface GuiListLocalization {

	translation?: GuiListTranslation;

	translationResolver?: (key: string, value: string) => string;

}

export type GuiListTranslation = TranslationChange;

export const GuiListDefaultTranslation = defaultTranslation;
