import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { SourceWarehouse } from '../../../source/core/api/source.warehouse';
import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { ItemEntity } from '../../../source/core/domain/core/item/item.entity';
import { StructureId } from '../../../core/api/structure.id';
import { ListViewTemplate } from './template/list-view-template';
import { ListViewTemplateArchive } from './template/list-view-template.archive';

@Component({
	selector: 'div[gui-list-view-source]',
	templateUrl: './list-view-source.component.html',
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ListViewSourceComponent extends SmartComponent implements OnInit, OnDestroy {

	source: Array<any> = [];

	template: ListViewTemplate;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly structureSourceWarehouse: SourceWarehouse,
				private readonly containerTemplateArchive: ListViewTemplateArchive) {
		super(changeDetectorRef, elementRef);
		this.addClassToHost('gui-block');
	}

	ngOnInit() {

		this.hermesSubscribe(
			this.structureSourceWarehouse.onEntities(this.structureId),
			(items: Array<ItemEntity>) => {
				this.source = items.map(i => i.getSourceItem());
			}
		);

		this.hermesSubscribe(
			this.containerTemplateArchive.on(),
			(template: ListViewTemplate) => {
				this.template = template;
			}
		);
	}

	protected getSelectorName(): string {
		return 'gui-list-view-source';
	}
}
