import { ListViewId } from '../list-view.id';
import { ListViewMode } from './list-view-mode';
import { ListViewDomainEvent } from '../list-view.domain-event';


export class ListViewModeSetEvent extends ListViewDomainEvent {

	constructor(schemaId: ListViewId,
				private readonly mode: ListViewMode) {
		super(schemaId, mode, 'ListViewModeSetEvent');
	}

	getTheme(): ListViewMode {
		return this.mode;
	}
}
