import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { SourceWarehouse } from '../../../source/core/api/source.warehouse';
import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { ItemEntity } from '../../../source/core/domain/core/item/item.entity';
import { StructureId } from '../../../core/api/structure.id';
import { ListViewCardTemplate } from './template/list-view.card-template';
import { ListViewCardTemplateArchive } from './template/list-view.card-template.archive';


@Component({
	selector: 'div[gui-list-container-card]',
	templateUrl: './list-view.container-card.component.html',
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ListViewContainerCardComponent extends SmartComponent implements OnInit {

	items: Array<any> = [];

	cardTemplate: ListViewCardTemplate;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly structureSourceWarehouse: SourceWarehouse,
				private readonly listCardTemplateArchive: ListViewCardTemplateArchive) {
		super(changeDetectorRef, elementRef);

		this.addClassToHost('gui-flex');
		this.addClassToHost('gui-flex-wrap');
		this.addClassToHost('gui-m-0');
		this.addClassToHost('gui-p-0');
		this.addClassToHost('gui-rounded');

	}

	ngOnInit() {

		this.hermesSubscribe(
			this.structureSourceWarehouse.onEntities(this.structureId),
			(items: Array<ItemEntity>) => {
				this.items = items.map(i => i.getSourceItem());
			}
		);

		this.hermesSubscribe(
			this.listCardTemplateArchive.on(),
			(template: ListViewCardTemplate) => {
				this.cardTemplate = template;
				this.changeDetectorRef.detectChanges();
			}
		);
	}

	protected getSelectorName(): string {
		return 'gui-list-container-card';
	}

}
